.header {
    height: 99px;
    border-bottom: 1px solid $borderColor;
    .container{
        display: flex; 
        @media(max-width: 872px){
            flex-direction: column;
            justify-content: center;
        }
    }
}

.logo{
    background: url(../img/logo.png) center left no-repeat;
    display: flex;
    align-items: center;
     
    @media(max-width: 872px){
        height: 99px; 
    }
    a{
        font-size: 27px;
        line-height: 32px;
        font-weight: 300;
        margin-left: 60px;
        letter-spacing: 0.68px;
    }
    span{
        font-weight: 900;
        color: $themePageColor;
    }
}
.header_browseSearch{
        margin-left: 37px;
        display: flex;
        align-items: center;
    form {
        width: 404px;
        height: 36px;
        display: flex;
        align-items: center;
        border-radius: 3px;
        border: 1px solid $borderColor;
        overflow: hidden;
    }
    .browse {
        display: block;
        width: 86px;
        height: inherit;
        text-align: center;
        border-right: 1px solid $borderColor;
        background-color: #f8f8f8;
        color: $textColorMain;
        font-weight: 300;
        line-height: 35px;
        letter-spacing: 0.35px;

        &:hover{
            background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(235,235,235,1) 100%);
        }
        i{
            color: gray;
            margin-left: 8px;
        }
        &:active{
            color: $themePageColor;
        }
    }
    .searchtop{
        width: 264px;
        height: inherit;
        padding-left: 16px;
        border: none;
        outline: none;
        font-size: 13px;
        font-weight: 300;
        line-height: 34px;
        letter-spacing: 0.35px;
        background: none;

        &:focus{
            background-color: $borderColor;
        }
    }
    .buttopSearchTop{
        width: 36px;
        background: none;
        border: none;
        border-left: 1px solid $borderColor;
        height: inherit;
        color: gray;
        outline: none;

        &:hover{
            background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(235,235,235,1) 100%);
        }
        &:active{
            color: $themePageColor;
        }
    }
}

.headerCart_Account{
        display: flex;
        align-items: center;
        margin-left: auto;
    .cart{
        margin: 0 26px;
    }
    .myAccount{
        text-align: center;
        width: 130px;
        height: 38px;
        background-color: $themePageColor; 
        line-height: 38px;
        border-radius: 3px;

        &:active{
            background: linear-gradient(180deg, rgba(201,100,115,1) 0%, rgba(241,109,127,1) 100%);
        }
        i{
            margin-left: 8px;  
        }
        a{
            font-size: 15px;
            letter-spacing: 0.38px;
            color: white;
        }
    }
}
